import Axios from 'axios';
import {MOCK_PATH} from '../utils/dispatchEnv';

const getMock = (url) => {
  return (params) => Axios.get(url, {
    params,
    timeout: 8000
  });
};

const postMock = (url) => {
  return (params) => Axios.post(url, params, {
    timeout: 8000
  })
};

const addTimes = getMock(MOCK_PATH + '/addTimes');

export {
  addTimes
}



