<template>
    <div class="app-wrap">
        <loading></loading>
      </div>
</template>

<script type="text/ecmascript-6">
import modal from './components/modal';
import loading from './components/loadingView';
import {addTimes} from './mock/mock';
export default {
    name: 'app',
    data () {
        return {

        }
      },
      created () {
        this.initMock();
      },
      methods: {
        initMock () {
          this.getTestApi();
        },
        async getTestApi () {
          try {
            let resTest = await addTimes();
            console.log(resTest);
          } catch (e) {
            console.log(e);
          }
        }
      },
      components: {
        loading
      }
    };
</script>

<style rel="stylesheet/stylus" lang="stylus">
  @import "assets/css/index.styl";
  body
    position relative
    .app-wrap
      background-color #ccc
      position absolute
      width rpx(750)
      top 0
      left 50%
      margin-left rpx(-375)
      height 100%
      background-size 100% 100%
</style>



